@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
    @font-face {
        font-family: 'Manrope';
        font-style: normal;
        font-weight: 400;
        font-display: swap;
        src: url(./manrope.ttf) format('truetype');
    }
}
@layer base {
    html {
        font-family: "Manrope", system-ui, sans-serif;
    }
}
@layer utilities {
    .font-thin {
        font-variation-settings: 'wght' 100;
    }
    .font-extralight {
        font-variation-settings: 'wght' 200;
    }
    .font-light {
        font-variation-settings: 'wght' 300;
    }
    .font-normal {
        font-variation-settings: 'wght' 400;
    }
    .font-medium {
        font-variation-settings: 'wght' 500;
    }
    .font-semibold {
        font-variation-settings: 'wght' 600;
    }
    .font-bold {
        font-variation-settings: 'wght' 700;
    }
    .font-extrabold {
        font-variation-settings: 'wght' 800;
    }
    .font-black {
        font-variation-settings: 'wght' 900;
    }
}


table {
    @apply w-full bg-white rounded-lg ;
    @apply overflow-x-auto;

    > thead {
        tr {
            @apply border-b border-b-gray-600 text-nowrap;

            th {
                @apply text-gray-200 font-semibold text-sm leading-[18px] p-2 text-center border-l border-l-gray-600 overflow-hidden whitespace-nowrap text-ellipsis;
                &:first-of-type {
                    @apply border-none;
                }
            }
        }
    }
    tbody {
        tr {
            @apply border-b border-b-gray-600 text-nowrap;
            td {
                @apply text-black font-semibold text-sm text-center p-2 border-l border-l-gray-600 overflow-hidden whitespace-nowrap text-ellipsis  ;
                &:first-of-type {
                    @apply border-none;
                }
            }
        }
    }
}

.customRange {
    -webkit-appearance: none;
    appearance: none;

    @apply bg-gray-900 h-[1px];
    &::-webkit-slider-runnable-track, &::-moz-range-track {
        @apply bg-gray-900  w-full h-[2px] cursor-pointer ;

    }
     &::-moz-range-thumb {
        -webkit-appearance: none;
        @apply rounded-full outline-none border border-black w-6 h-11 z-20 bg-white relative;
        pointer-events: all;
    }

    &::-webkit-slider-thumb {
        -webkit-appearance: none;
        height: 44px;
        width: 24px;
        border-radius: 100%;
        z-index: 32;
        position: relative;
        background: white;
        border:1px solid black;
        pointer-events: all;

    }
}

.customDualRange {
    -webkit-appearance: none;
    appearance: none;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    input[type=range]::-moz-range-thumb {
        -webkit-appearance: none;
        appearance: none;
        @apply bg-black rounded-full outline-none border-none w-6 h-11 z-20;
        pointer-events: all;
    }
    input[type="range"] {
        -webkit-appearance: none;
        appearance: none;
        height: 2px;
        width: 100%;
        position: absolute;
        background-color: #D4D5D6;
        pointer-events: none;

    }
    input[type=range]::-webkit-slider-thumb {
        -webkit-appearance: none;
        height: 44px;
        width: 24px;
        border-radius: 100%;
        z-index: 32;
        position: relative;
        background: black;
        pointer-events: all;

    }
    input[type=range]::-webkit-slider-runnable-track {
        z-index: -1;
        position: relative;
    }

    .from {

        z-index: 2;
    }
    .to {

    }
}

